import { Component } from './base/Component';
export class Card extends Component {
    constructor(container, actions) {
        super(container);
        this._basketId = container.querySelector('.basket__item-index');
        this._category = container.querySelector('.card__category');
        this._title = container.querySelector('.card__title');
        this._image = container.querySelector('.card__image');
        this._description = container.querySelector(`.card__text`);
        this._button = container.querySelector(`.card__button`);
        this._price = container.querySelector('.card__price');
        if (actions.onClick) {
            if (this._button)
                this._button.addEventListener('click', actions.onClick);
            else
                container.addEventListener('click', actions.onClick);
        }
    }
    set id(value) {
        this.container.dataset.id = value;
    }
    get id() {
        return this.container.dataset.id || '';
    }
    set basketId(value) {
        this._basketId.textContent = value;
    }
    get basketId() {
        return this._basketId.textContent || '';
    }
    categoryColor(value) {
        switch (value) {
            case 'софт-скил':
                return 'card__category_soft';
            case 'хард-скил':
                return 'card__category_hard';
            case 'кнопка':
                return 'card__category_button';
            case 'дополнительное':
                return 'card__category_additional';
            default:
                return 'card__category_other';
        }
    }
    set category(value) {
        this.setText(this._category, value);
        this.toggleClass(this._category, this.categoryColor(value));
    }
    get category() {
        return this._category.textContent || '';
    }
    set title(value) {
        this.setText(this._title, value);
    }
    get title() {
        return this._title.textContent || '';
    }
    set image(value) {
        this.setImage(this._image, value, this.title);
    }
    set description(value) {
        if (Array.isArray(value)) {
            this._description.replaceWith(...value.map((str) => {
                const descTemplate = this._description.cloneNode();
                this.setText(descTemplate, str);
                return descTemplate;
            }));
        }
        else
            this.setText(this._description, value);
    }
    set button(value) {
        if (this._button)
            this._button.textContent = value;
    }
    set price(value) {
        if (value === null)
            this.setText(this._price, 'Бесценно');
        else
            this.setText(this._price, `${value.toString()} синапса(-ов)`);
    }
    get price() {
        return Number(this._price.textContent || '');
    }
}
