import { Form } from './common/Form';
import { ensureAllElements } from '../utils/utils';
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._paymentButton = ensureAllElements('.button_alt', container);
        this._paymentButton.forEach((button) => button.addEventListener('click', () => this.selected(button.name)));
    }
    set address(value) {
        this.container.elements.namedItem('address').value =
            value;
    }
    set email(value) {
        this.container.elements.namedItem('email').value =
            value;
    }
    set phone(value) {
        this.container.elements.namedItem('phone').value =
            value;
    }
    selected(name) {
        this._paymentButton.forEach((button) => this.toggleClass(button, 'button_alt-active', button.name === name));
        this.events.emit('order:change', { name });
    }
}
